@import url(https://fonts.googleapis.com/css?family=Merriweather+Sans|Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  font-size: 62.5%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* .App {
  font-size: 62.5%;
} */

h1, h2, h3 {
  font-family: 'Merriweather Sans', Arial, Helvetica, sans-serif;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.5rem;
  margin-top: 2rem;
  margin-bottom: 4rem;
}

h3 {
  font-size: 2.5rem;
}

p {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-size: 2rem;
}

i {
  font-size: 3rem;
  margin-right: 1rem;;
}

a {
  text-decoration: none;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-size: 2rem;
  color: black;
}

.fa-html5 {
  color: #e34f26;
}
  
.fa-css3-alt {
  color: #1572b6;
}

.fa-js {
  color: #f7df1e;
}

.fa-less {
  color: #214674;
}

.fa-git-alt {
  color: #f05033;
}

.fa-python {
  color: #376f9e;
}

.fa-react {
  color: #376f9e;
}

.f {
  font-size: 7rem;
  margin-right: 3rem;
  max-height: 5rem;
  margin-bottom: 3rem;
}

a:hover {
  color: rgb(40, 40, 40);
}

.click {
  font-size: 1.7rem;
  color: darkblue;
}
